import React, { Component } from "react";
import "./App.scss";
import champions from "./champion.json";
import Header from "./utility/Header";
import Modal from "./utility/Modal";
import Login from "./components/login/Login";
import InfoPageItem from "./components/login/InfoPageItem";
import SummonersSelector from "./components/login/SummonersSelector";
import List from "./utility/List";
import ChampionListItem from "./championItems/ChampionListItem";

export const roles = ["Top", "Jungle", "Mid", "Bottom", "Support"]

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      randomChampions: [],
      showModal: "",
      releases: []
    };
    this.toggleModal = this.toggleModal.bind(this);
    this.rerollChampion = this.rerollChampion.bind(this);
  }

  async componentDidMount() {
    let releases = await fetch(
      "https://api.github.com/repos/spoof14/araf/releases"
    );
    releases = await releases.json();

    this.setState({
      randomChampions: this.rollChampions(5),
      releases
    });
  }
  
  toggleModal(modal) {
    this.setState({
      showModal: modal
    });
  }

  onClick = name => this.setState({ [name]: !this.state[name] });

  onChangesClick = async () => {
    this.setState({ seeChanges: !this.state.seeChanges });
  };

  render() {
    let { randomChampions } = this.state;

    return (
      <div className="App">
        <Header title="All Random All Fill" />

        <List 
          Component={ChampionListItem} 
          items={randomChampions} 
          listName="champion-list" 
          onClick={this.rerollChampion}
        />
      </div>
    );
  }

  rollChampions() {
    let champs = [];
    while (champs.length < 5) {
      let element = this.rollChampion();

      if (!this.someChampIsSame(champs, element)) champs.push(element);
    }
    return champs;
  }

  rollChampion() {
    var random = Math.floor(Math.random() * Object.keys(champions.data).length);
    var key = Object.keys(champions.data)[random];
    var element = champions.data[key];
    return { name: element.name, image: element.image.full };
  }

  someChampIsSame(array, newChamp) {
    return array.some(champ => champ.name === newChamp.name);
  }

  rerollChampion(index) {
    console.log('index', index)
    let { randomChampions } = this.state;
    let currChamp = randomChampions[index];
    let newChamp = currChamp;
    console.log(currChamp)
    while (
      newChamp === currChamp ||
      this.someChampIsSame(randomChampions, newChamp)
    ) {
      newChamp = this.rollChampion();
    }
    randomChampions[index] = newChamp;
    console.log(randomChampions[index])
    this.setState({
      randomChampions
    });
  }
}

export default App;
