import React, { PureComponent } from 'react'

import { roles } from '../App';

export default class ChampionListItem extends PureComponent {
	

	onClick = () => {this.props.onClick(this.props.index)}
	render() {
		const {item, index} = this.props

		return (
			<div
			  key={item.name}
			  className="champion-list-item"
			  onClick={this.onClick}
			>
			  <span>{item.name}</span>
			  <img
				src={require(`../championImages/${item.image}`)}
				alt="champion"
			  ></img>
			  <span>{roles[index]}</span>
			</div>
		  );
	}
}
