import React, { PureComponent } from 'react'

export default class SummonersSelector extends PureComponent {
	
	onChange = (e) => this.setState({[e.target.name]:e.target.value})
	render() {
		return (
			<div>
				<input type="number" name="numberOfChampions" min={1} max={5} onChange={this.onChange}></input>
			</div>
		)
	}
}
